	width: 100%;
	margin-top: 1rem;
}

.store-products__title {
	font-size: 1.5rem;
	font-weight: 600;
	margin: 0 0 16px;
	color: var(--color-dark);
}

.store-products__grid {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
	gap: 24px;
	width: 100%;
}

.products-empty {
	text-align: center;
	color: #666;
	font-size: 1.1rem;
	padding: 32px 0;
}
