___CSS_LOADER_EXPORT___.push([module.id, `.batch {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	justify-items: center;
	width: 100%;
	min-height: 0;
}

.stores-grid {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 20px;
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: 0 10px;
	box-sizing: border-box;
}

.batch-header {
	width: 100%;
	text-align: center;
	margin-bottom: 20px;
}

.batch-stats {
	font-size: 14px;
	color: var(--color-gray);
}

.batch-loading {
	text-align: center;
	padding: 40px;
	color: var(--color-gray);
}

.batch-load-more {
	display: block;
	margin: 20px auto;
	padding: 12px 24px;
	background: var(--color-accent);
	color: white;
	border: none;
	border-radius: 6px;
	cursor: pointer;
	font-size: 16px;
}

.batch-load-more:hover {
	background: var(--color-accent-dark);
}

.batch-end {
	text-align: center;
	padding: 20px;
	color: var(--color-gray);
	font-style: italic;
}

.batch-error {
	background: var(--color-error-light);
	color: var(--color-error);
	padding: 16px;
	border-radius: 8px;
	margin-bottom: 20px;
	border: 1px solid var(--color-error);
}

.batch-retry-btn {
	background: var(--color-error);
	color: white;
	border: none;
	padding: 8px 16px;
	border-radius: 4px;
	cursor: pointer;
	margin-top: 8px;
}

.batch-empty {
	text-align: center;
	padding: 40px;
	color: var(--color-gray);
}
