___CSS_LOADER_EXPORT___.push([module.id, `.avatar-upload {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	align-items: center;
}

.avatar-upload__preview {
	width: 10rem;
	height: 10rem;
	border-radius: var(--border-radius-large);
	overflow: hidden;
	border: 2px solid var(--color-input-stroke);
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: border-color 0.3s;
	position: relative;
}

.avatar-upload__preview:hover {
	border-color: var(--color-accent);
}

.avatar-upload__input {
	position: absolute;
	opacity: 0;
	width: 100%;
	height: 100%;
	cursor: pointer;
}

.avatar-upload__image {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.avatar-upload__placeholder {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 0.5rem;
	color: var(--color-text-secondary);
	font-size: 0.9rem;
	text-align: center;
}

.avatar-upload__placeholder-icon {
	font-size: 1.5rem;
	color: var(--color-accent);
}

.avatar-upload__field {
	width: 100%;
}

.avatar-upload__field input {
	width: 100%;
	padding: 0.5rem;
	border: 1px solid var(--color-input-stroke);
	border-radius: var(--border-radius-small);
	background: var(--color-background);
	color: var(--color-text);
	font-size: 1rem;
}

.avatar-upload__error {
	color: var(--color-err);
	font-size: 0.9rem;
	text-align: center;
