___CSS_LOADER_EXPORT___.push([module.id, `.cart-item {
	display: flex;
	align-items: center;
	gap: 16px;
	padding: 16px;
	background: rgba(255, 255, 255, 0.7);
	border-radius: var(--border-radius-medium);
	backdrop-filter: blur(4px);
	border: 1px solid rgba(0, 0, 0, 0.05);
}

.cart-item__image {
	width: 60px;
	height: 60px;
	object-fit: cover;
	border-radius: var(--border-radius-small);
	flex-shrink: 0;
}

.cart-item__info {
	flex: 1;
	min-width: 0;
}

.cart-item__name {
	font-size: 1rem;
	font-weight: 600;
	color: var(--color-dark);
	margin-bottom: 4px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.cart-item__price {
	font-size: 0.95rem;
	color: var(--color-primary);
	font-weight: 600;
}

.cart-item__controls {
	display: flex;
	align-items: center;
	gap: 8px;
}

.cart-item__control-btn {
	width: 32px;
	height: 32px;
	border: none;
	background: var(--color-accent);
	color: var(--color-light);
	border-radius: 50%;
	font-size: 1.1rem;
	font-weight: bold;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: background-color 0.2s;
}

.cart-item__control-btn:hover {
	background: var(--color-accent-hover);
}

.cart-item__quantity {
	min-width: 24px;
	text-align: center;
	font-weight: 600;
	color: var(--color-dark);
}
