import { AvatarForm } from '../components/AvatarForm/AvatarForm'
import { Button } from '../components/Button/Button'
import { Footer } from '../components/Footer/Footer'
import { Navbar } from '../components/Navbar/Navbar'
import { ChangePasswordForm } from '../components/PasswordChangeForm/PasswordChangeForm'
import { PersonalInfo } from '../components/PersonalInfo/PersonalInfo'
import { defineComponent } from '../framework/component'
import { authManager } from '../modules/authManager'
import { profileApi, UpdateProfileRequest } from '../modules/profileApi'
import { navigate } from '../modules/router'

interface ProfilePageState {
	email: string
	fullName: string
	city: string
	street: string
	house: string
	building: string
	apartment: string
	comment: string
	isLoading: boolean
}

export const ProfilePage = defineComponent({
	state(): ProfilePageState {
		return {
			email: '',
			fullName: '',
			city: '',
			street: '',
			house: '',
			building: '',
			apartment: '',
			comment: '',
			isLoading: false,
		}
	},

	async onMounted() {
		await this.loadUserProfile()
	},

	async loadUserProfile() {
		const user = authManager.getUser()
		if (!user) {
			console.log('  ')
			return
		}

		this.updateState({ isLoading: true })

		try {
			const response = await profileApi.getProfile(user.id)
			if (response.data) {
				const profile = response.data
				const addressParts = this.parseAddress(profile.address || '')

				this.updateState({
					email: profile.email || '',
					fullName: profile.name || '',
					region: addressParts.region,
					city: addressParts.city,
					street: addressParts.street,
					house: addressParts.house,
					building: addressParts.building,
					apartment: addressParts.apartment,
					comment: '',
					isLoading: false,
				})
			}
		} catch (error) {
			console.error('  :', error)
			this.updateState({ isLoading: false })
		}
	},

	parseAddress(address: string) {
		const parts = address.split(',')
		return {
			region: parts[0]?.trim() || '',
			city: parts[1]?.trim() || '',
			street: parts[2]?.trim() || '',
			house: parts[3]?.trim() || '',
			building: parts[4]?.trim() || '',
			apartment: parts[5]?.trim() || '',
		}
	},

	formatAddress(): string {
		const { region, city, street, house, building, apartment } = this.state
		return [region, city, street, house, building, apartment]
			.filter(part => part.trim() !== '')
			.join(', ')
	},

	handleFieldChange(field: string, value: string) {
		this.updateState({ [field]: value } as Partial<ProfilePageState>)
	},

	async handleSaveProfile() {
		const user = authManager.getUser()
		if (!user) {
			console.error('  ')
			return
		}

		this.updateState({ isLoading: true })

		try {
			const updates: UpdateProfileRequest = {
				name: this.state.fullName,
				address: this.formatAddress(),
			}

			const response = await profileApi.updateProfile(user.id, updates)

			if (response.status === 200) {
				console.log('  ')
			} else {
				throw new Error('  ')
			}
		} catch (error) {
			console.error('  :', error)
		} finally {
			this.updateState({ isLoading: false })
		}
	},

	async handlePasswordChange(current: string, newPassword: string) {
		const user = authManager.getUser()
		if (!user) {
			console.error('  ')
			return
		}

		try {
			const updates: UpdateProfileRequest = {
				current_password: current,
				new_password: newPassword,
			}

			const response = await profileApi.updateProfile(user.id, updates)

			if (response.status === 200) {
				console.log('  ')
				return
			} else {
				throw new Error('  ')
			}
		} catch (error) {
			console.error('  :', error)
			throw error
		}
	},

	render() {
		const user = authManager.getUser()

		if (this.state.isLoading) {
			return (
				<div class="profile-page">
					<Navbar
						userAuthed={true}
						onLogoClick={() => navigate('/')}
						onLoginClick={() => navigate('/auth')}
					/>
					<div style={{ textAlign: 'center', padding: '2rem' }}>
						<h2> ...</h2>
					</div>
					<Footer />
				</div>
			)
		}

		return (
			<div class="profile-page">
				<Navbar
					userAuthed={true}
					onLogoClick={() => navigate('/')}
					onLoginClick={() => navigate('/auth')}
					onCartClick={() => navigate('/profile')}
				/>

				<div
					style={{
						display: 'flex',
						'flex-direction': 'column',
						width: 'fit-content',
						margin: '1.5rem auto',
						justifyContent: 'center',
						gap: '1rem',
					}}
				>
					<div
						style={{
							display: 'flex',
							width: 'fit-content',
							margin: '1.5rem auto',
							boxSizing: 'border-box',
							flexWrap: 'wrap',
							justifyContent: 'center',
							gap: '2rem',
						}}
					>
						<form
							on={{
								submit: (e: Event) => {
									e.preventDefault()
									this.handleSaveProfile()
								},
							}}
						>
							<h2 style={{ 'margin-bottom': '0.75rem' }}> </h2>
							<PersonalInfo
								email={this.state.email}
								fullName={this.state.fullName}
								region={this.state.region}
								city={this.state.city}
								street={this.state.street}
								house={this.state.house}
								building={this.state.building}
								apartment={this.state.apartment}
								comment={this.state.comment}
								onFieldChange={(f, v) => this.handleFieldChange(f, v)}
								onSave={() => this.handleSaveProfile()}
							/>
						</form>

						<div
							style={{
								display: 'flex',
								'flex-direction': 'column',
								gap: '0.75rem',
							}}
						>
							<h2> </h2>
							<AvatarForm />
							<h2> </h2>
							<ChangePasswordForm
								onSubmit={(current: string, newPassword: string) =>
									this.handlePasswordChange(current, newPassword)
								}
							/>
						</div>
					</div>
					<Button
						type="button"
						variant="accent"
						text=""
						onClick={() => navigate('/')}
					/>
					<Button
						type="button"
						variant="error"
						text="  "
						onClick={() => navigate('/')}
					/>
				</div>
				<Footer />
			</div>
		)
	},
})
